.main {
    margin: 0 auto;
    margin-bottom: 144px;

    @include sm {
        min-height: 800px;
        margin-bottom: 50px; } }
.bg-main {
    position: absolute;
    width: 100%;
    top: 22%;
    height: 40%;
    background-color: #1d354e;
    transform: skewY(30deg) translateX(-50%);
    left: 50%;
    z-index: -2;
    @include md {
        top: 18%;
        height: 40%; }
    @include sm {
        top: 10%;
        height: 35%; } }
.forecast-wrapper {
    max-width: 980px;
    position: relative;
    // transform: skewY(-30deg)
    @include md {
        padding: 20px 20px; } }

.forecast-block {
    margin-top: 140px;
    font-weight: 400;
    color: #ffffff;
    @include sm {
        margin-top: 30px; }
    &__title {
        font-size: 60px;
        @include md {
            font-size: 48px; }
        @include sm {
            font-size: 30px; } }
    &__desc {
        font-size: 24px;
        @include md {
            font-size: 18px; }
        @include sm {
            font-size: 14px; } } }

.forecast-block__currency {
    padding: 50px 0; }

.value {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    font-weight: 700;
    box-sizing: border-box;
    &__item {
        font-size: 30px;
        border: 8px solid #223e5c;
        border-radius: 8px;
        padding: 13px 29px;
        cursor: pointer;
        @include md {
            font-size: 18px;
            border: 6px solid #223e5c; }
        @include sm {
            width: 30%;
            text-align: center;
            padding: 15px 0px;
            margin-bottom: 10px;
            font-size: 16px; } }
    &__item:hover {
        background-color: #00bdca;
        border-color: #00bdca; }
    &__link {
        text-decoration: none;
        color: #ffffff; } }

.graph {
    // transform: skewY(-30deg)
    margin: 0 auto;
    display: flex;
    justify-content: center;
    position: relative;
    max-width: 1900px;
    z-index: -1;
    &__img_lg {
        width: 100%;
        height: 100%;
        @include md {
            display: none; } } }

.graph__img_md {
    display: none;
    @include md {
        width: 100%;
        height: 100%;
        display: block; }
    @include sm {
        display: none; } }

.graph__img_sm {
    display: none;
    @include sm {
        width: 100%;
        height: 100%;
        display: block; } }

.value-now {
    display: flex;
    position: relative;
    z-index: 1;
    top: 92px;
    left: 58px;
    text-align: right;
    max-width: 365px;
    justify-content: space-between;
    @include sm {
        position: static; }
    &__item {
        font-size: 24px;
        font-weight: 700;
        @include sm {
            font-size: 18px; } }
    &__item:nth-child(n+2) div:first-child {
        font-size: 12px;
        font-weight: 500;
        @include sm {
            font-size: 12px; } }
    &__item:nth-child(3) div:last-child {
        font-size: 12px;
        color: #7eec4e;
        font-weight: 500;
        @include sm {
            font-size: 12px; } } }


.value-now__item:first-child {
    align-self: center; }

.main:before {
    content: url(../img/forecast-box.png);
    position: absolute;
    left: 0px;
    @include xl {
        display: none; } }

.forecast-box:after {
    content: url(../img/forecast-box2.png);
    position: absolute;
    top: -15%;
    right: -330px;
    @include md {
        top: -54%;
        right: 78px; }
    @include sm {
        display: none; } }

.margin-graph {
    margin: 0 10px; }
