.video {

    border-radius: 16px;
    background-color: #ffffff;
    position: relative;
    z-index: -1; }



.video-btn {
    content: url(../img/play_btn.png);
    color: #fff;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
    z-index: 4;
    @include sm {
        width: 60px;
        height: 60px; }
    @include sm:hover {
        background-color: black; } }
.video video::-webkit-media-controls-panel {
    display: none; }

.video-wrapper {
    max-width: 1200px;
    height: auto;
    margin: 0 10px;
    padding: 50px;
    @include md {
        padding: 30px; }
    @include sm {
        padding: 10px;
        padding-bottom: 5px; } }

.video video {
    width: 100%;
    height: 100%;
    border-radius: 8px;
    position: relative;
    z-index: 3; }
