
.links {
    display: flex;
    justify-content: space-between;
    margin: 40px 0;
    @include sm {
        flex-direction: column;
        margin: 80px 0 40px 0; } }
.links__block {
    display: flex;
    @include sm {
        margin: 0 10px;
        margin-bottom: 15px; } }

.links__item {
    margin-right: 12px; }

.copy__link {
    color: #ffffff;
    font-size: 18px;
    font-weight: 600;
    text-decoration: none;
    margin-right: 40px;
    @include sm {
        font-size: 16px; } }

.copy__link:last-child {
    margin: 0; }

.bg-footer {
    position: absolute;
    width: 92%;
    height: 1000px;
    background-color: #1d354e;
    transform: skewY(30deg) translateX(-50%);
    left: -30px;
    z-index: -2;
    @include sm {
        left: 184px;
        top: 102%;
        width: 270%; } }

.ft {
    position: relative;
    // overflow: hidden
    @include md {
        padding: 0 20px; }
    @include sm {
        padding: 0px; } }

.footer-box:before {
    content: url(../img/footer-box.png);
    position: absolute;
    left: -344px;
    top: -391px;
    @include md {
        left: 220px;
        top: 495px; }
    @include sm {
        top: -124%;
        left: 74%; } }

