#burger-menu:checked ~ .header-wrapper {
  position: absolute;
  width: 100%;
  top: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  justify-content: space-around;
  flex-direction: column-reverse;
  padding: 10px 0;
}

.hidden-menu {
  display: none;
}

.btn-menu {
  display: none;
  color: #fff;
  cursor: pointer;
  transition: linear 2s;
  z-index: 3;
  width: 25px;
}

@media only screen and (max-width: 767px) {
  .btn-menu {
    position: absolute;
    right: 15px;
    display: block;
  }
}

.btn-menu span {
  display: block;
  height: 3px;
  background-color: #fff;
  margin: 5px 0 0;
  transition: all .1s linear 0.2s;
  position: relative;
  transition: 0.5s;
}

.hidden-menu:checked ~ .btn-menu span.first {
  transform: rotate(45deg);
  top: 10px;
  transition: 0.5s;
}

.hidden-menu:checked ~ .btn-menu span.second {
  opacity: 0;
}

.hidden-menu:checked ~ .btn-menu span.third {
  transform: rotate(-45deg);
  top: -6px;
  transition: 0.5s;
}

