.tokensHero-wrapper {
    padding: 0 30px;
    @include sm {
        padding: 0 5px; } }
.tokensHero {
    padding: 65px 95px 75px 75px;
    background-color: #ffffff;
    box-shadow: 0 24px 72px rgba(29, 53, 78, 0.25);
    border-radius: 16px;
    position: relative;
    margin-bottom: 90px;
    @include sm {
        margin-top: 25px; }
    @include md {
        padding: 36px 48px 48px 36px; }
    @include sm {
        padding: 26px 22px 35px 22px; }
    &__title {
        color: #1d354e;
        font-size: 72px;
        font-weight: 600;
        @include md {
            font-size: 48px; }
        @include sm {
            font-size: 36px; } }
    &__desc {
        color: #1d354e;
        font-size: 18px;
        font-weight: 300;
        line-height: 36px;
        padding: 25px 0;
        @include md {
            font-size: 14px;
            line-height: 22px;
            padding: 15px 0; }
        @include sm {
            padding-bottom: 25px; } }

    &__subtitle {
        color: #1d354e;
        font-size: 36px;
        font-weight: 600;
        @include sm {
            font-size: 22px; } } }

.tokensHero__desc:nth-child(3) {
    @include sm {
        display: none; } }

.tokensHero-form {
    border-radius: 8px;
    border: 8px solid #56ecf6;
    margin: 45px 0;
    position: relative;

    @include md {
        margin: 35px 0; }
    @include sm {
        margin: 25px 0; }
    &__text {
        background-color: #ffffff;
        width: 100%;
        color: #1d354e;
        font-size: 24px;
        font-weight: 700;
        padding: 26px 154px 26px 35px;
        display: block;
        text-overflow: ellipsis;
        width: 100%;
        overflow: hidden;
        @include md {
            font-size: 18px;
            padding: 24px 100px 24px 25px; }
        @include sm {
            padding: 15px 75px 15px 15px; } } }


.tokensHero-form:after {
    content: url(../img/form-img.png);
    position: absolute;
    background-color: #56ecf6;
    top: 50%;
    transform: translateY(-50%);
    right: -2px;
    padding: 30px 30px 30px 32px;
    cursor: pointer;
    border-radius: 0 8px 8px 0;
    @include md {
        padding: 20px 22px 20px 24px; }
    @include sm {
        padding: 15px 18px 15px 20px; } }

.tokensHero__btn {
    padding: 22px 133px;
    margin-right: 48px;
    @include md {
        margin-right: 26px;
        padding: 21px 95px; }
    @include sm {
        display: block;
        margin: 0 auto;
        // padding: 15px 0px
        padding: 21px 95px;
        margin-bottom: 16px; }
    @include ms {
        width: 100%;
        padding: 15px; } }

.tokensHero__btn:last-child {
    padding: 19px 118px;
    background-color: #ffffff;
    color: #1d354e;
    border-radius: 32px;
    border: 3px solid #1d354e;
    margin-right: 0;
    @include md {
        padding: 18px 80px; }
    @include sm {
        margin: 0 auto; }
    @include ms {
        padding: 12px; } }

.tokensHero:before {
    content: url(../img/box-tokens.png);
    position: absolute;
    right: 26%;
    top: -5%;
    @include md {
        right: -8%;
        top: 3%; }
    @include sm {
        display: none; } }

.tokensHero:after {
    content: url(../img/box-header2.png);
    position: absolute;
    right: -18%;
    top: 40%;
    transform: translateY(-50%);
    @include md {
        right: -21%;
        top: 115%; }
    @include sm {
        display: none; } }
