main {
    margin: 0 10px;
    @include md {
        padding: 0 30px; }
    @include sm {
        padding: 0px; } }
.roadmap-wrapper {
    max-width: 980px;
    margin: 0 auto; }

.bg-roadmap {
    height: 2730px;
    @include md {
        height: 1875px; }
    @include sm {
        height: auto;
        position: relative;
        top: -119px; } }

.bg-roadmap:before {
    content: url(../img/roadmap.png);
    position: absolute;
    z-index: -1;
    left: 50%;
    transform: translateX(-25%);
    @include md {
        content: url(../img/roadmap-tablet2.png);
        position: absolute;
        z-index: -1;
        left: 50%;
        transform: translateX(-50%); }
    @include sm {
        content: url(../img/Roadmap1.png);
        position: absolute;
        z-index: 0;
        left: 50%;
        transform: translateX(-42%); } }

.benefits__title {
    max-width: 374px;
    color: #fefeff;
    font-size: 72px;
    line-height: 60px;
    letter-spacing: 3.6px;
    padding-top: 90px;
    @include md {
        font-size: 56px;
        line-height: 45px; }
    @include sm {
        font-size: 36px;
        line-height: 34px;
        letter-spacing: 0px;
        max-width: 250px;
        padding: 0;
        position: relative;
        top: 100px;
        font-weight: 500; }
    span {
        font-size: 48px;
        letter-spacing: 2px;
        @include md {
            font-size: 36px; }
        @include sm {
            letter-spacing: 0px;
            font-size: 28px; } } }


.benefits-block {
    display: flex;
    flex-direction: column;
    // margin-top: 155px
    @include md {
 }        // margin-top: 200px
    @include sm {
        margin-top: 225px;
        margin-left: -123px; }
    &__subtitle {
        color: #fefeff;
        font-size: 36px;
        font-weight: 600;
        line-height: 40px;
        @include md {
            font-size: 27px;
            line-height: 30px; } }
    &__text {
        color: #fefeff;
        font-size: 18px;
        font-weight: 300;
        line-height: 36px;
        @include md {
            font-size: 14px;
            line-height: 22px; } }
    &__ico img {
        margin: 5px 0px 0 0;
        @include md {
 }            // margin: -50px 22px 0 0
        @include sm {
            display: none; } } }

