.footer-wrapper {
    max-width: 990px;
    position: relative;
    @include md {
        margin: 0 20px; }
    @include sm {
        margin: 0 10px; } }
.subscribe {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 60px;
    @include sm {
        flex-direction: column-reverse; } }

.subscribe-telegram__item {
    display: block;
    font-size: 18px;
    font-weight: 600;
    text-decoration: none;
    padding: 19px 0px;
    background-color: #ffffff;
    border-radius: 32px;
    text-align: center;
    color: #1d354e;
    margin-bottom: 24px;
    @include md {
        padding: 18px 37px; } }

.btn_xl {
    width: 370px;
    @include sm {
        max-width: 290px; } }

.btn_white:hover {
    background-color: #1d354e;
    color: #ffffff;
    transition: 0.4s; }
.subscribe-telegram__link:before {
    content: url(../img/telegram.svg);
    position: relative;
    top: 5px;
    right: 10px; }

.block-button {
    @include sm {
        margin-bottom: 50px; } }
.block-button__link {
    font-size: 18px;
    color: #ffffff;
    font-weight: 600;
    text-decoration: none;
    display: block;
    text-align: center; }

.block-email {
    padding: 65px;
    border-radius: 8px;
    border: 16px solid #56ecf6;
    @include md {
        padding: 50px 18px;
        border: 12px solid #56ecf6; }
    @include sm {
        margin-bottom: 55px; } }


.block-email__title {
    max-width: 350px;
    font-size: 60px;
    line-height: 60px;
    font-weight: 200;
    color: #ffffff;
    margin-bottom: 30px;
    span {
        font-weight: 600; }
    @include md {
        font-size: 48px;
        line-height: 48px;
        max-width: 275px; }
    @include sm {
        font-size: 36px;
        line-height: 36px;
        max-width: 220px; } }

.email-form__input {
    background: 0;
    width: 370px;
    height: 40px;
    font-size: 30px;
    color: #ffffff;
    padding: 2px 10px;
    border-bottom: 2px solid #56ecf6;
    border-radius: 1px;
    padding-bottom: 20px;
    margin-bottom: 48px;
    @include md {
        max-width: 280px; }
    @include sm {
        max-width: 230px; } }

::-webkit-input-placeholder {
    color: #ffffff;
    font-size: 24px;
    font-weight: 600;
    @include md {
        font-size: 18px; } }

.block-email__btn {
    padding: 21px 142px;
    display: flex;
    justify-content: center;
    @include md {
        padding: 21px 0px;
        margin: 0 auto; } }
